body {
  background: #eee;
}
.container {
  padding: 20px;
}
/**
 * 以下用于styles任务中进行注入
 */
.app-header {
  background: #303039;
  height: 48px;
  position: relative;
}
.app-header .button {
  padding: 0;
  margin: 0;
  height: 36px;
  line-height: 36px;
}
.app-header .app-header-middle {
  width: 200px;
  text-align: center;
  margin: 0 auto;
  position: relative;
}
.app-header .app-header-middle a {
  display: block;
  height: 48px;
  line-height: 48px;
  font-size: 18px;
  color: #fff;
}
.app-header .app-header-left {
  position: absolute;
  top: 6px;
  left: 6px;
  width: 200px;
  text-align: left;
}
.app-header .app-header-left .btn-login {
  width: 44px;
}
.app-header .app-header-right {
  position: absolute;
  top: 6px;
  right: 6px;
  width: 200px;
  text-align: right;
}
.app-header .app-header-right .btn-edit {
  width: 44px;
}
.app-header .app-header-right .btn-edit i {
  font-size: 20px;
}
